.hero-comingsoon {
  height: calc(100vh - 74px);
}

.hero-comingsoon .pitch {
  height: 100%;
  display: flex;
  align-items: center;
}

.hero-comingsoon .title {
  font-family: "Syne", serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 58px;
  letter-spacing: 0;
  text-align: left;
  margin-bottom: 15px;
  text-shadow: 1px 1px 5px #ffffff;
}

.hero-comingsoon .title span {
  color: var(--brand-color);
  font-family: "Syne", serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 58px;
  letter-spacing: 0;
  text-align: left;
}

.hero-comingsoon .subtitle {
  font-family: "Inter", serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 15px;
  text-shadow: 1px 1px 5px #ffffff;
}

.hero-comingsoon .call-to-action {
  /* background: var(--brand-color); */
  border-radius: 8px;
  border: 1px solid var(--brand-color);
  color: var(--brand-color);
  padding: 6px 0;
  font-family: "Inter", serif;
  text-align: center;
  display: inline-block;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 140%;
  width: 150px;
  cursor: pointer;
  margin: 0 0 5px 5px;
  transition: 0.2s ease-in-out;
}

.hero-comingsoon .call-to-action.main {
  background: var(--brand-color);
  color: #fff;
}

.hero-comingsoon .call-to-action:hover {
  border-color: var(--brand-color);
  background-color: var(--brand-color);
  color: #fff;
}

.hero-comingsoon .call-to-action.main:hover {
  border-color: var(--brand-color);
  background-color: #fff;
  color: var(--brand-color);
}

@media (max-width: 600px) {
  .hero-comingsoon .title {
    font-size: 40px;
    line-height: 120%;
  }

  .hero-comingsoon .title span {
    font-size: 40px;
    line-height: 120%;
  }

  .hero-comingsoon .subtitle {
    font-size: 16px;
    line-height: 1.5;
  }
}
